.app {
}
.app > div + div {
  border-left: 1px dashed #ccc;
}
.nav {
  height: 100vh;
  overflow: auto;
  padding: 5px;
  display: none;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

@media screen and (min-width: 960px) {
  .app {
    display: grid;
    grid-template-columns: 240px auto;
    gap: 5px;
  }
  .nav {
    display: flex;
  }
  .patient-info {
    display: flex;
  }
}
